html {
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(56%, rgb(25, 27, 32)), to(rgb(63, 84, 97)));
  background-image: linear-gradient(180deg, rgb(25, 27, 32) 56%, rgb(63, 84, 97) 100%);
  height: 100%;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(0,0,0,0)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  /* background-color: #282c34; */
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

@media screen and (min-width: 360px) and (max-width: 767px) {
  div {
    font-size: 0.6rem;
  }
  ul.list-group {
    height: 261px;
  }
  ul.list-group.current-video {
    height: 100px;
    border: 1px solid rgb(81, 249, 249, 0);
  }
  ul.related-group {
    height: 348px;
  }
  button.button.buttonNext {
    font-size: 12px;
  }
  button.button {
    font-size: 9px;
  }
  .swiper-slide {
    width: 100%
  }
  h4 {
    font-size: 1.4em;
  }
  h5 {
    font-size: 1.2em;
  }
  .media-object {
    width: 80px;
    height: 60px;
  }
  button.listOperations {
    position: absolute;
    bottom: 15px;
    left: 100px;
  }
  table.listOperations {
    position: absolute;
    bottom: 15px;
    left: 100px;
  }
  .swiper-wrapper {
    align-items: center;
  }
  div.search-container {
    width: 95%;
    margin: 0 auto;
    align-items: center;
  }
  div.search-bar {
    margin: 21px;
  }
  div.playlist-container {
    width: 95%;
    margin: 0 auto;
    align-items: center;
  }
  .iframeContainer {
    height: 200px;
  }
  img.imgLogo {
    width: 95%;
  }
  .search-field {
    padding-top: 0px
  }
  div.search-bar {
    margin: 23px;
    text-align: center;
  }
  div.search-bar input {
    width: 90%;
    font-size: 12px;
    outline: rgba(53, 60, 73, 1) solid 2px
  }
  input.playlistInput {
    width: 60%;
  }
  div.addedPadding {
    padding-top: 15px;
  }
  div.related {
    padding-top: 10px;
    padding-bottom: 0px;
  }
  div.settingsContainer {
    background: rgba(83, 94, 114, 1);
    border: 1px solid #4e4e4e;
    color: rgb(24, 24, 24);
    display: flex;
    flex-direction: column;
  }
  div.settingsItem {
    background: rgb(177, 185, 201);
    border: 1px solid #4e4e4e;
    vertical-align: middle;
    line-height: 33px;
    text-align: center;
  }
  div.settingsContainer>div {
    display: flex;
    width: 100%;
    height: 36px;
    margin: auto;
  }
  p {
    margin: auto;
    /* Important */
    text-align: center;
    font-size: 18px;
  }
  input[type="number"] {
    height: 32px;
    font-size: 18px;
  }
  label {
    margin: auto;
    /* Important */
    text-align: center;
    display: block;
    padding-left: 15px;
    text-indent: -15px;
    font-size: 18px;
  }
  input[type="checkbox"] {
    width: 32px;
    height: 32px;
    padding: 0;
    margin: 0;
    vertical-align: bottom;
    position: relative;
    *overflow: hidden;
    font-size: 18px;
  }
  label>input[type="text"] {
    width: 70%;
    height: 32px;
    padding: 0;
    margin: 0;
    vertical-align: bottom;
    top: -1px;
    *overflow: hidden;
    font-size: 18px;
  }
  button.link {
    font-size: 12px;
  }
}

